import Vue from "vue";
import axios from "axios";
import store from "../store/index";

axios.defaults.baseURL = "https://server.sp0meur.com";
// axios.defaults.baseURL = "http://localhost:3000";
axios.defaults.validateStatus = (status) => status >= 200 && status < 399;

axios.interceptors.request.use(
  async function(config) {
    // Do something before request is sent
    let accessToken = await store.getters.getToken;
    let isLoggedIn = store.getters.isLoggedIn;
    if (isLoggedIn === true) {
      config.headers.common["x-auth-token"] = accessToken;
      return config;
    } else {
      if (isLoggedIn === true) {
        store.dispatch("destroySession");
        Vue.prototype.$notify({
          group: "errors",
          type: "error",
          title: "token is expird",
          text: "destroySession ",
        });
      }
      return config;
    }
  },
  function(error) {
    return Promise.reject(error);
  }
);
axios.interceptors.response.use(
  function(response) {
    return response;
  },
  function(error) {
    if (!error.response) {
      Vue.prototype.$notify({
        group: "errors",
        type: "error",
        title: "problem in server",
        text: "problem in server try again",
      });
    } else {
      /// err
    }
    return Promise.reject(error);
  }
);

Vue.prototype.$http = axios;
