<template>
  <v-container class="col-md-5">
    <div class=" success">
      <h2 class="white--text text-center">
        Groupe Chat youmer <v-spacer></v-spacer>
        <v-btn text fab @click="logout" height="0" width="0">
          {{ this.$store.getters.getUserName }} <v-icon>mdi-logout</v-icon>
        </v-btn>
      </h2>
    </div>
    <v-divider class="ma-2"></v-divider>
    <div class=" pa-3" style="border:1px solid grey;height:100vh">
      <div style="height:80%;overflow-y: auto" class="pa-3 chat__messages">
        <v-card
          v-for="(item, items) in getData"
          :key="items"
          :class="
            item.userId == $store.getters.getUserId
              ? 'pa-1 col-md-10 mt-4 float-left'
              : 'pa-1 col-md-10 float-right  mt-3 text-right red white--text'
          "
          style="border-radius:15px;clear:both"
          light
        >
          <strong>{{ item.user.username_u }}</strong>
          <p>{{ item.msgchat }}</p>
          <span
            style="font-size:9px"
            :class="
              item.userId === $store.getters.getUserId
                ? 'float-right'
                : 'float-left'
            "
            ><timeago
              class="float-center grren--text"
              :datetime="item.createdAt"
              :auto-update="60"
            ></timeago
          ></span>
        </v-card>
        <div v-if="typing" class="ma-2 red--text">
          <strong> {{ typs }} is typing</strong>
        </div>
      </div>
      <div class="messagesend ">
        <v-textarea
          class="col-md-12"
          label="tap here for send ...."
          rows="1"
          v-model="msg"
          @input="emitTyping"
          @keydown.enter.exact.prevent
          @keyup.enter.exact="sendMessage()"
          prepend-icon="mdi-comment"
        ></v-textarea>

        <div class="sending">
          <v-btn
            class=""
            fab
            dark
            small
            color="success "
            @click="sendMessage()"
          >
            <v-icon dark>
              mdi-send
            </v-icon>
          </v-btn>
        </div>
      </div>
    </div>
  </v-container>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      getData: [],
      msg: "",
      typing: false,
      typs: "",
    };
  },
  sockets: {
    connect: function() {
      console.log("socket connected");
    },
    data: function(data) {
      this.getData = data;
    },
    recev: function(data) {
      this.soundEffect.play();
      this.getData.push(data);
    },
    typings(data) {
      clearTimeout(this.timeOutId);
      this.typing = true;
      this.typs = data.nickname;
      this.timeOutId = setTimeout(() => {
        this.typing = false;
      }, 2000);
      return data;
    },
  },
  methods: {
    emitTyping() {
      this.scrollBottom();
      this.$socket.emit("typing", {
        nickname: this.$store.getters.getUserName,
      });
    },
    async logout() {
      // this.$socket.close();
      await this.$store.dispatch("destroySession");
    },
    sendMessage: async function() {
      let today = new Date();
      var dd = String(today.getDate()).padStart(2, "0");
      var mm = String(today.getMonth() + 1).padStart(2, "0"); //January is 0!
      var yyyy = today.getFullYear();
      var H = today.getHours();
      var m = today.getMinutes();
      var s = today.getSeconds();
      // 2021-05-03 23:10:05
      today = yyyy + "-" + mm + "-" + dd + " " + H + ":" + m + ":" + s;

      this.$socket.emit("add", {
        msgchat: this.msg,
        userId: this.$store.getters.getUserId,
        username: this.$store.getters.getUserName,
        createdAt: today,
      });
      this.getData.push({
        msgchat: this.msg,
        userId: this.$store.getters.getUserId,
        user: { username_u: this.$store.getters.getUserName },

        createdAt: today,
      });
      this.scrollBottom();

      let data = await this.$http.post("/c/add", {
        msgchat: this.msg,
        userId: this.$store.getters.getUserId,
      });
      console.log(data);
      this.msg = "";
    },
    scrollBottom: () => {
      let messagesContainer = document.querySelector(".chat__messages");
      setTimeout(() => {
        messagesContainer.scrollTop = messagesContainer.scrollHeight;
      }, 0);
    },
  },
  beforeMount() {
    this.scrollBottom();
    this.$http.get("/c/chat", {}).then((res) => {
      this.getData = res.data;
    });
  },
};
</script>
<style>
.messagesend {
  position: absolute;
  bottom: 1px;
  width: 35%;
}
.sending {
  position: absolute;
  right: 5px;
  bottom: 50px;
}
@media (max-width: 800px) {
  .messagesend {
    width: 90%;
  }
}
</style>
