import Vue from "vue";
import router from "../../router/index";

export default {
  state: {
    accessToken: localStorage.getItem("access_token") || "",
    user: JSON.parse(localStorage.getItem("user")) || {},
  },
  getters: {
    isLoggedIn: (state) =>
      !!state.accessToken &&
      !!state.accessToken.match(Vue.prototype.$pattern.jwtToken),
    getToken: (state) => state.accessToken,
    getUserId: (state) => state.user.id,
    getUserName: (state) => state.user.username,
    getEmail: (state) => state.user.email,
    getcreatedAt: (state) => state.user.createdAt,
    getupdateAt: (state) => state.user.updatedAt,
  },
  mutations: {
    AUTH_SUCCESS: (state, payload) => {
      state.accessToken = payload.accessToken;
      state.user = payload.user;
      console.log(payload);
      console.log(state.accessToken);
      localStorage.setItem("access_token", payload.accessToken);
      localStorage.setItem("user", JSON.stringify(payload.user));
    },
    DESTROY_SESSION: (state) => {
      localStorage.removeItem("access_token");
      localStorage.removeItem("user");
      state.accessToken = "";
      state.user = {};
    },
  },
  actions: {
    signIn: async (context, payload) => {
      return new Promise((resolve, reject) => {
        Vue.prototype.$http
          .post("/auth/login", payload)
          .then(async (res) => {
            if (res.status === 200 && res.data.code === 200) {
              await context.commit("AUTH_SUCCESS", {
                accessToken: res.data.token,
                user: res.data.user,
              });
              resolve(res);
            }
          })
          .catch((err) => {
            reject(err);
          });
      });
    },

    signUp: (context, payload) => {
      return new Promise((resolve, reject) => {
        Vue.prototype.$http
          .post("/auth/create", payload)
          .then((res) => {
            resolve(res);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
    destroySession: async (context) => {
      context.commit("DESTROY_SESSION");
      await router.push({ name: "Login" });
    },
    async handleNotFound() {
      await router.push({ name: "NotFound" });
    },
  },
  modules: {},
};
